* {
    margin: 0;
    padding: 0;
  }
  
  article {
    width: 40%;
    margin-left: 5%;
    margin-top: 2%;
  }
  
  .cart_box img {
    width: 100px;
    height: 100px;
  }
  .cart_img {
    display: flex;
    width: 400px;
  }
  
  .cart_img p {
    font-weight: bold;
    margin-left: 12px;
  }
  button{
    cursor:pointer;
  }
  .cart_box {
    display: flex;
    align-items: center;
    justify-content:space-between;
    margin-top: 10%;
    padding-bottom: 5px;
  }
  
  .cart_box div:nth-child(3) span {
    background-color: lightgreen;
    padding: 5px 10px;
    border-radius: 5px;
    margin-right: 10px;
  }
  
  .total {
    display: flex;
    justify-content: space-between;
  }
  
  .total span:nth-child(1) {
    font-size: 2rem;
    color: blue;
    font-weight: bold;
    letter-spacing: 2px;
  }
  
  .total span:nth-child(2) {
    font-size: 2rem;
    color: green;
    font-weight: bold;
  }
  