.center{
    display:flex;
}
.left{
    width:30%;
    height: 230vh;
    margin-top: 5%;
    display: flex;
    flex-direction: column;
}

.btn{
    margin:30px;
    padding:40px;
    background-color: rgb(0, 0, 255);
    color:rgb(255, 255, 255);
    transition: 0.5s ease-in-out;
    border: none;
    letter-spacing: 1px;
    font-weight: 700;

    
}

.btn:hover{
    border-radius: 25px;
}
  h1{
    text-align: center;
  }
  
  h2{
    text-align: center;
    margin-top:10px;
    color:rgb(128, 0, 128);
  }
